import React, { useState } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';

export default function Calculatrice() {
   const [v,setv]=useState("");
const supp=()=>{
  let v1=v.replaceAll(' ', '');
  setv(v1.slice(0, -1))
}
  const value=()=>{
let v1=v.replaceAll('²', '**2')
let v2=v1.replaceAll('³','**3')
let vl=eval(v2);
setv(vl)
 }
  
  return (
    <div className='d-flex justify-content-center'>
    <div className=' border bg-white border-dark  w-25 '>
 <div className=' card card-header text-center py-2 mb-1'>{v} </div>
 <div className="container ">
  <div className="row row-cols-2 row-cols-lg-4 border-dark  ">
  <button className="col btn btn-outline-dark " onClick={supp}>Supp</button>
  <button className="col btn btn-outline-dark " onClick={(e)=>setv("")} >C</button>
  <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+' % ')}>%</button>
  <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+' / ')}>/</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+'7')}>7</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+'8')}>8</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+'9')}>9</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+' * ')}>*</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+'4')}>4</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+'5')}>5</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+'6')}>6</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+' - ')}>-</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+'1')}>1</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+'2')}>2</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+'3')}>3</button>
    <button className="col btn btn-outline-dark" onClick={(e)=>setv(v+' + ')}>+</button>
    <button className="col btn btn-outline-dark"onClick={(e)=>setv(v+'.')}>,</button>
    <button className="col btn btn-outline-dark"onClick={(e)=>setv(v+'0')}>0</button>
    <button className="col btn btn-outline-dark"onClick={(e)=>setv(v+'²')}>x<sup>2</sup></button>
    <button className="col btn btn-info" onClick={value}>=</button>
  </div>
</div>
    </div>
</div>

  )
}
